.App {
  font-family: "Helvetica", sans-serif;
  color: rgb(57, 56, 56);
  .allItems {
    display: flex;
    align-items: center;
    padding: 15px 0 15px 0;
    border-bottom: 1px solid #e3e3e3;
    &-p {
      margin-left: 8px;
    }
  }
}

.modules {
  display: flex;
  &-one {
    width: 50%;
    border-right: 1px solid #e3e3e3;
  }
  &-two {
    padding-left: 10px;
    width: 50%;
  }
}
